import React from "react"
import { Global, css } from "@emotion/core"

const globalStyles = css`
  #___gatsby {
    height: 100%;
    width: 100%;
  }

  #gatsby-focus-wrapper {
    height: 100%;
  }
`

const GlobalLayout = ({ children }) => {
  return (
    <>
      <Global styles={globalStyles} />
      {children}
    </>
  )
}

export default GlobalLayout
